import React, { useState, useEffect } from "react";
function FormInput({ handleChange, formInputData, handleSubmit }) {
  var [date, setDate] = useState(new Date());
  formInputData.checkIn=date.toLocaleTimeString()
  useEffect(() => {
    var timer = setInterval(() => setDate(new Date()), 1000);
    return function cleanup() {
      clearInterval(timer);
    };
  });
  return (
    //< className="position-absolute">

    <div
      className="form-row row md-3 "
      style={{
        margin: "10px",
        padding: "20px",
        borderStyle: "outset",
        borderRadius: "10pt",
      }}
    >
      <div className="col">
        <input
          type="text"
          onChange={handleChange}
          value={formInputData.rollNumber}
          name="rollNumber"
          className="form-control"
          placeholder="Roll Number"
        />
      </div>
      <div className="col">
        <input
          type="text"
          onChange={handleChange}
          value={formInputData.fullName}
          name="fullName"
          className="form-control"
          placeholder="Full Name"
        />
      </div>
      <div className="col">
        <label
          type="time"
          onChange={handleChange}
          value={formInputData.checkIn}
          name="checkIn"
          className="form-control"
          placeholder="Check In"
          required
        >{formInputData.checkIn} </label>
      </div>
      <div className="col">
        <input
          type="time"
          onChange={handleChange}
          value={formInputData.checkOut}
          name="checkOut"
          className="form-control"
          placeholder="Check Out"
          required
        />
      </div>
      <div className="col">
        <input
          type="submit"
          onClick={handleSubmit}
          className="btn btn-primary"
        />
      </div>
    </div>
  );
}

export default FormInput;
