import React from "react";
function Table({tableData}){
    return(
        
        <table className="table table-bordered table-hover">
            <thead>
                <tr>
                    <th className="table-warning">S.N</th>
                    <th className="table-warning">Roll Number</th>
                    <th className="table-warning">Full Name</th>
                    <th className="table-warning">Check In</th>
                    <th className="table-warning">Check Out</th>
                </tr>
            </thead>
            <tbody>
            {
                tableData.map((data, index)=>{
                    return(
                        <tr key={index}>
                            <td>{index+1}</td>
                            <td>{data.rollNumber}</td>
                            <td>{data.fullName}</td>
                            <td>{data.checkIn}</td>
                            <td>{data.checkOut}</td>
                        </tr>
                    )
                })
            }
            </tbody>
        </table>
    )
}

export default Table;